import React from "react";
import favContext from "./favcontext";
import { useState } from "react";


const FavState = (props) => {
    const host = "https://localhost:3001"
    const aut = ""
    const favlist = [

    ]
    const [fav, setFav] = useState(favlist)
    const [err, setErr] = useState([])
    
    



    //get user data
    const getuser = async () => {
        const response = await fetch('http://localhost:3001/api/auth/getuser', {
            method: 'POST',
            headers: {
                "Content-Type": "application/json",
                "auth-token": localStorage.getItem('token')
            },
        });
        const json = await response.json()
        console.log(json.name)
        const username = json.name
        localStorage.setItem('username', username)
    }
    

    //fetch fav
    const fetchfav = async () => {
        const response = await fetch('http://localhost:3001/api/favourites/fetchall', {
            method: 'GET',
            headers: {
                "Content-Type": "application/json",
                "auth-token": localStorage.getItem('token')
            },
        });
        const json = await response.json()
        console.log(json)
        setFav(json)
    }

    //delete fav
    const deletefav = async (id) => {
        const response = await fetch(`http://localhost:3001/api/favourites/deletefav/${id}`, {
            method: 'DELETE',
            headers: {
                "Content-Type": "application/json",
                "auth-token": localStorage.getItem('token')
            },
        });
        const json = await response.json()
        console.log(json)

        console.log("deleting fav with id" + id)
    }

    //show alert
    
    
    
    
    

    return (
        <favContext.Provider value={{ fav, setFav, deletefav, fetchfav, getuser}}>
            {props.children}
        </favContext.Provider>
    )
}

export default FavState;