import React from 'react'
import { useEffect, useState } from 'react'
import { useContext } from 'react'
import favContext from '../Context/Favourites/favcontext'

const Favourites = () => {
    const context = useContext(favContext)
    const { fav, deletefav, fetchfav, user } = context;
    useEffect(() => {
        fetchfav()
        // eslint-disable-next-line
    }, [])

    console.log(fav)
    let uname = localStorage.getItem('username')


    return (
        <div>
            <h3 className='text-light py-2'>Hello {uname}!</h3>
            <div>
            {fav.map((item) => {
                return (
                    <div className="card mb-3 " style={{ maxWidth: 'none' }}>
                        <div className="row g-0">
                            <div className="col-md-1">
                                <img src={item.imageId} className="img-fluid rounded-start" style={{ maxHeight: '100px' }} alt="..." />
                            </div>
                            <div className="col-md-8">
                                <div className="card-body">
                                    <a href={`/info/${item.titleId}`}><h5 className="card-title">{item.title}</h5></a>
                                    <p className="card-text">{item.genre}</p>
                                </div>
                            </div>

                            <div className="col-md-2">
                                <div className="card-body">
                                    <h6 className="card-text">Your rating: {item.rating}</h6>
                                </div>
                            </div>

                            <div className="col-md-1">
                                <div className="card-body">
                                    <a className="btn btn-danger my-1" href='/favourites' onClick={() => { deletefav(item._id) }} role="button">Remove</a>
                                </div>


                            </div>
                        </div>
                    </div>
                )
            })}
            </div>
            
        </div>




    )
}

export default Favourites