import React from 'react';
import './App.css';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Navbar from './Components/Navbar';
import Mainpage from './Components/Mainpage';
import Footer from './Components/Footer';
import Movies from './Components/Movies';
import Searchfeed from './Components/Searchfeed';
import Animedetail from './Components/Animedetail';
import Test from './Components/Test';
import Filter from './Components/Filter';
import Format from './Components/Format';
import Login from './Components/Login';
import FavState from './Context/Favourites/favstate';
import Favourites from './Components/Favourites';
import Signup from './Components/Signup';
import Alert from './Components/Alert';
import { useContext } from 'react';
import { useState } from 'react';
import favContext from './Context/Favourites/favcontext';




function App() {
  const [alert, setAlert] = useState(null);

  const showalert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    })
    setTimeout(() => {
      setAlert(null)
    }, 2000);
  }
  return (
    <div>

      <BrowserRouter>
        <body className="bg-dark " >
          
          <FavState>
            <Navbar />
            
            <Routes>
              <Route path="/" exact element={<Mainpage/>} />
              <Route path="/AnimeTube-anime-website" exact element={<Mainpage/>} />
              <Route path="/info/:animeid" element={<Animedetail showalert={showalert} />} />
              <Route path="/movies" element={<Movies format="MOVIE" sort="POPULARITY_DESC" label="Movies" />} />
              <Route path="/popular" element={<Format format="TV" sort="POPULARITY_DESC" label="Popular" />} />
              <Route path="/trending" element={<Format format="TV" sort="TRENDING_DESC" label="Trending" />} />
              <Route path="/filter" element={<Filter />} />
              <Route path="/search/:searchTerm" element={<Searchfeed />} />
              <Route path="/test" element={<Test />} />
              <Route path="/login" element={<Login showalert={showalert} />} />
              <Route path="/signup" element={<Signup showalert={showalert} />} />
              <Route path="/favourites" element={<Favourites />} />
              
            </Routes>
            
          </FavState>
          <Footer />
        </body>
      </BrowserRouter>
    </div>
  )

};

export default App;
