import React, { useEffect } from 'react'
import axios from 'axios';
import { useContext } from 'react';
import favContext from '../Context/Favourites/favcontext';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';



const Signup = (props) => {


    const [credentials, setCredentials] = useState({ name: "", email: "", password: "", cpassword: "" })


    const navigate = useNavigate();
    console.log(credentials.name)
    console.log(credentials.email)
    console.log(credentials.password)



    const context = useContext(favContext);
    const { err } = context;
    const onchange = (e) => {
        setCredentials({...credentials, [e.target.name]: e.target.value})
    }

    const onsignin = async (e) => {
        e.preventDefault();
        const {name, email, password} = credentials
        const response = await fetch('http://localhost:3001/api/auth/createuser', {
            method: 'POST',
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({ name, email, password })
        });
        const json = await response.json()
        console.log(json)
        if(json.success){
            //save auth token
            localStorage.setItem('token', json.authtoken);
            navigate("/login")
            props.showalert("Account created successfully", "success")
        }
        else{
            props.showalert("Invalid credentials", "danger")
        }
            
        
        
    

    }


    return (
        <div className="card" style={{ width: '18rem', marginTop: '5.5rem', marginLeft: '39rem', marginBottom: '9rem' }}>
            <div className="card-body">
                <h5 className="card-title text-center">Sign In</h5>
                <form onSubmit={onsignin}>
                    <label for="exampleFormControlTextarea1" class="form-label">Userame</label>
                    <textarea class="form-control" id="name" name='name' rows="1" value={credentials.name} onChange={onchange} required></textarea>
                    <label for="exampleFormControlInput1" className="form-label">Email address</label>
                    <input type="email" className="form-control" id="email" name='email' placeholder="name@example.com" value={credentials.email} onChange={onchange} required></input>
                    <label for="exampleFormControlInput1" className="form-label">Password</label>
                    <input type="password" className="form-control" name='password' id="password" value={credentials.password} onChange={onchange} required></input>
                    <label for="exampleFormControlInput1" className="form-label"> Confirm Password</label>
                    <input type="password" className="form-control" name='cpassword' id="cpassword" ></input>
                    <button type="submit" className="btn btn-primary my-2">Sign In</button>
                    
                    

                </form>


                <a href="/login" className="card-link">Already have an account? Log In</a>

            </div>
        </div>
    )
}

export default Signup