import React, { useEffect } from 'react'
import axios from 'axios';
import { useContext } from 'react';
import favContext from '../Context/Favourites/favcontext';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';



const Login = (props) => {
    

    const [credentials, setCredentials ] = useState({email:"", password:""})
    

    const navigate = useNavigate();
    console.log(credentials.email)
    console.log(credentials.password)
    
    

    const context = useContext(favContext);
    const {getuser} = context;  
    

    const onchange = (e) => {
        setCredentials({...credentials, [e.target.name]: e.target.value})
    }

    const onlogin = async (e) => {
        e.preventDefault();
        const response = await fetch('http://localhost:3001/api/auth/login', {
            method: 'POST',
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({email: credentials.email, password: credentials.password})
        });
        const json = await response.json()
        console.log(json)
        if(json.success){
            //save auth token
            localStorage.setItem('token', json.authtoken);
            navigate("/")
            props.showalert("Logged in successfully", "success")
            getuser();
        }
        else{
            props.showalert("Invalid credentials", "danger")
        }
      }
    
    
    return (
        <div className="card" style={{ width: '18rem', marginTop: '5.5rem', marginLeft: '39rem', marginBottom: '9rem' }}>
            <div className="card-body">
                <h5 className="card-title text-center">Log In</h5>
                <form onSubmit={onlogin}>
                    <label for="exampleFormControlInput1" className="form-label">Email address</label>
                    <input type="email" className="form-control" id="email" name='email' placeholder="name@example.com" value={credentials.email} onChange={onchange} required></input>
                    <label for="exampleFormControlInput1" className="form-label">Password</label>
                    <input type="password" className="form-control" name='password' id="password" value={credentials.password} onChange={onchange} required></input>
                    <button type="submit" className="btn btn-primary my-2">Log In</button>
                    
                </form>


                <a href="/signup" className="card-link">Create an account</a>
                
            </div>
        </div>
    )
}

export default Login