import React from 'react'

const Footer = () => {
    return (
        <div className="card text-center text-light bg-black " style={{}}>
            <div className="card-header">
                AnimeTube
            </div>
            <div className="card-body">
                <h5 className="card-title">Watch HD anime for free on AnimeTube.com</h5>
                <p className="card-text">Disclaimer: This site does not store any files on its server. All contents are
                    provided by non-affiliated third parties.</p>
            </div>

        </div>
    )
}

export default Footer