import React from 'react'
import axios from 'axios';
import { useState, useEffect } from 'react'
import { fetchFromAPI } from '../Utilities/fetchFromAPI';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Alert } from 'bootstrap';



const images = [{
    id: 1,
    src: "https://s4.anilist.co/file/anilistcdn/media/anime/banner/113415-jQBSkxWAAk83.jpg",
    title: "Jujutsu Kaisen",
    alt: "Image 1",
    link: 113415
},
{
    id: 2,
    src: "https://s4.anilist.co/file/anilistcdn/media/anime/banner/11757-TlEEV9weG4Ag.jpg",
    title: "Sword Art Online",
    alt: "Image 2 ",
    link: 11757
},
{
    id: 3,
    src: "https://s4.anilist.co/file/anilistcdn/media/anime/banner/101922-YfZhKBUDDS6L.jpg",
    title: "Kimetsu No Yaiba",
    alt: "Image 3",
    link: 101922
},
{
    id: 4,
    src: "https://s4.anilist.co/file/anilistcdn/media/anime/banner/18897-h3TRFmkGYLkd.jpg",
    title: "Nisekoi",
    alt: "Image 4",
    link: 18897
}
];

const options1 = {
    method: 'POST',
    url: 'https://anilist-graphql.p.rapidapi.com/',
    headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
        'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
    },
    data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $sort: [MediaSort]) \
    {\
    Page(page: $page, perPage: $perPage) {\
    pageInfo {\
      total\
      currentPage\
      lastPage\
      hasNextPage\
      perPage\
    }\
    media(id: $id, type: $type, sort: $sort ) {\
      id\
      isAdult\
      title {\
        \
        english\
      }\
      \
      coverImage {\
        large\
      } \
      \
      averageScore\
    }\
        \
        \
    }\
    }\",\"variables\":{\"sort\":\"POPULARITY_DESC\",\"page\":1,\"perPage\":12,\"type\":\"ANIME\"}}`
};

const options2 = {
    method: 'POST',
    url: 'https://anilist-graphql.p.rapidapi.com/',
    headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
        'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
    },
    data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $sort: [MediaSort]) \
    {\
    Page(page: $page, perPage: $perPage) {\
    pageInfo {\
      total\
      currentPage\
      lastPage\
      hasNextPage\
      perPage\
    }\
    media(id: $id, type: $type, sort: $sort ) {\
      id\
      isAdult\
      title {\
        \
        english\
      }\
      \
      coverImage {\
        large\
      } \
      \
      averageScore\
    }\
        \
        \
    }\
    }\",\"variables\":{\"sort\":\"TRENDING_DESC\",\"page\":1,\"perPage\":12,\"type\":\"ANIME\"}}`

};

const options3 = {
    method: 'POST',
    url: 'https://anilist-graphql.p.rapidapi.com/',
    headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
        'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
    },
    data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $genre_in: [String], $sort: [MediaSort], $format: MediaFormat, $status:MediaStatus  \
        ) \
        {\
          Page(page: $page, perPage: $perPage) {\
            pageInfo {\
              total\
              currentPage\
              lastPage\
              hasNextPage\
              perPage\
            }\
            media(id: $id, type: $type, genre_in: $genre_in, sort:$sort, format: $format, status: $status ) {\
              id\
              episodes\
              updatedAt\
              nextAiringEpisode {\
                id\
              }\
              title {\
                \
                userPreferred\
              }\
              \
             \
              \
        \
            }\
           \
            \
          }\
        }\",\"variables\":{\"page\":1,\"perPage\":6,\"type\":\"ANIME\",\"sort\":\"POPULARITY_DESC\",\"format\":\"TV\",\"status\":\"RELEASING\"}}`

}



const Mainpage = () => {

    const [anime, setAnime] = useState([]);
    const [trending, setTrending] = useState([]);
    const [latest, setLatest] = useState([]);
    const [visuality, setVisuality] = useState('visually-hidden')
    const settings = {
        infinite: true,
        dots: true,
        slidesToShow: 1,
        slidesToScroll: 1,
        lazyLoad: true,
        autoplay: true,
        autoplaySpeed: 5000,

    };

    useEffect(() => {
        axios.request(options1).then(function (response) {
            setAnime(response.data);
        }).catch(function (error) {
            console.error(error);
        });

        axios.request(options2).then(function (response) {
            setTrending(response.data);
        }).catch(function (error) {
            console.error(error);
        });

        axios.request(options3).then(function (response) {
            setLatest(response.data);
        }).catch(function (error) {
            console.error(error);
        });
    }, [])

    {/*console.log(anime?.data?.Page?.media)*/ }
    console.log(anime)
    console.log(trending)
    console.log(latest)
    console.log(visuality)

    function hover() {
        setVisuality('')
    }

    function nothover() {
        setVisuality('visually-hidden')
    }

    return (
        <div className='container'>
            <div className="row my-3" style={{ marginLeft: '-85px', marginRight: '-80px' }}>
                <div className="col-sm-8">
                    <Slider {...settings} style={{ marginLeft: '0px' }}>
                        {images.map((item) => (
                            <div key={item.id}>
                                <Link to={`/info/${item.link}`}>
                                    <img src={item.src} alt={item.alt} style={{ height: '300px', width: '950px' }} />
                                </Link>

                                <div style={{ background: 'linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(32,63,144,1) 35%, rgba(0,255,252,1) 100%)', marginRight: '0px' }}>
                                    <h5 style={{ color: '#fff', paddingLeft: '30px', paddingTop: '20px', paddingBottom: '20px' }}>{item.title}</h5>

                                    <a className="btn btn-primary" href={`/info/${item.link}`} role="button" style={{ position: 'absolute', marginLeft: '50rem', marginTop: '-60px', borderRadius: '20px', paddingLeft: '25px', paddingRight: '25px' }}>Watch</a>
                                </div>
                            </div>
                        ))}
                    </Slider>

                </div>
                {/**login box */}
                <div className="col-sm-4">
                    <h1 style={{ color: '#fff' }}>Latest Episodes</h1>
                    <ul className="list-group bg-dark ">

                        {latest?.data?.Page?.media.map((item, idx) => {
                            return (
                                <Link to={`/info/${item?.id}`}>
                                    <li className="list-group-item bg-dark border-dark text-light ">{item?.title?.userPreferred}  <span className="badge bg-secondary">New Episodes</span></li>
                                </Link>

                            )
                        })
                        }

                    </ul>
                </div>
            </div>

            <div className='container'>
                <div className="row" style={{}}>
                    <div className="col-sm-12">
                        <div className='container'>
                            <span><h1 style={{ color: '#fff' }}>Popular</h1><Link style={{ position: 'absolute', color: '#fff', marginLeft: '11rem', marginTop: '-40px' }} to="/popular">View All</Link></span>
                            <div className='row'>


                                {anime.length === 0 ? <div className="d-flex justify-content-center my-5">
                                    <div className="spinner-border text-primary" role="status">
                                        <span className="visually-hidden">Loading...</span>
                                    </div>
                                </div> : anime?.data?.Page?.media.map((item, idx) => {
                                    return (
                                        <div className='col-sm-2 my-2 '>
                                            <div className="card  border-light border-2 " id='hahaha' style={{ width: '11rem', height: '18rem', }} key={idx} >
                                                <Link to={`/info/${item.id}`} onClick={() => (window.location.href = '#top')}>
                                                    <img src={item?.coverImage?.large} title={item?.title?.english} className="card-img-top" alt={item?.title?.english} style={{ maxHeight: '178px' }} />
                                                </Link>
                                                <div className="card-body text-light bg-black">
                                                    <h6 className="card-title">{item?.title?.english.length > 30 ? item?.title?.english.slice(0, 30) + '...' : item?.title?.english}</h6>

                                                    {/*<a href="/info/{{i.animeId}}" className="btn btn-primary">Watch</a>*/}
                                                </div>
                                            </div>


                                        </div>
                                    )

                                })}
                            </div>

                            <span><h1 style={{ color: '#fff' }} className="my-2">Trending</h1><Link style={{ position: 'absolute', color: '#fff', marginLeft: '11rem', marginTop: '-40px' }} to="/trending">View All</Link></span>
                            <div className='row'>
                                {trending.length === 0 ? <div className="d-flex justify-content-center my-5">
                                    <div className="spinner-border text-primary" role="status">
                                        <span className="visually-hidden">Loading...</span>
                                    </div>
                                </div> : trending?.data?.Page?.media.map((item, idx) => {
                                    return (
                                        <div className='col-sm-2 my-2'>
                                            <div className="card  border-light border-2" style={{ width: '11rem', height: '18rem', }} key={idx} >
                                                <Link to={`/info/${item.id}`} onClick={() => (window.location.href = '#top')}>
                                                    <img src={item?.coverImage?.large} title={item?.title?.english} className="card-img-top" alt={item?.title?.english} style={{ maxHeight: '178px', }} />
                                                </Link>
                                                <div className="card-body text-light bg-black">
                                                    <h6 className="card-title">{item?.title?.english == null ? item?.title?.english : item?.title?.english.slice(0, 30) + ""}</h6>
                                                    <p className="card-text"></p>
                                                    {/*<a href="/info/{{i.animeId}}" className="btn btn-primary">Watch</a>*/}
                                                </div>
                                            </div>
                                        </div>
                                    )




                                })}
                            </div>





                            {/*<a className="btn btn-dark btn-outline-light py-2" href="/popular/2" role="button">See more</a>*/}
                        </div>

                    </div>


                </div>
            </div>
        </div>



    )
}

export default Mainpage