import React from 'react';
import { useState, useEffect } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';





const Filter = () => {

    const [newgenre, setNewgenre] = useState('Action');
    const [result, setResult] = useState([]);
    const [format, setFormat] = useState('TV');


    useEffect(() => {
        const res = {
            method: 'POST',
            url: 'https://anilist-graphql.p.rapidapi.com/',
            headers: {
                'content-type': 'application/json',
                'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
                'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
            },
            data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $genre_in: [String], $sort: [MediaSort], $format: MediaFormat \
        ) \
        {\
          Page(page: $page, perPage: $perPage) {\
            pageInfo {\
              total\
              currentPage\
              lastPage\
              hasNextPage\
              perPage\
            }\
            media(id: $id, type: $type, genre_in: $genre_in, sort:$sort, format:$format ) {\
              id\
              title {\
                \
                userPreferred\
              }\
              genres\
              coverImage {\
                large\
              } \
              \
        \
            }\
           \
            \
          }\
        }\",\"variables\":{\"page\":1,\"perPage\":24,\"type\":\"ANIME\",\"format\":\"${format}\",\"sort\":\"POPULARITY_DESC\",\"genre_in\":["${newgenre}"]}}`
        };

        axios.request(res).then(function (response) {
            setResult(response.data);
        }).catch(function (error) {
            console.error(error);
        });


    }, [newgenre, format])




    let nextId = 0
    const [genre, setGenre] = useState('Any');
    const [list, setlist] = useState([]);

    const handleChange2 = (event) => {
        setResult([]);
        setFormat(event.target.value)
    }

    const handleChange = (event) => {
        setResult([]);
        setGenre(event.target.value);
        setNewgenre(event.target.value);
    }

    const listChange = () => {

        setlist([
            ...list,
            { id: nextId++, genre: genre }
        ]);

    }
    function removedup(list) {
        var unique = [];
        list.forEach(element => {
            if (!unique.includes(element.genre)) {
                unique.push(element.genre)
            }
        });
        return unique
    }

    //console.log(list)
    console.log(removedup(list));

    let match = removedup(list);
    console.log(match)
    console.log(newgenre)
    console.log(result)
    console.log(format)


    return (
        <div className="container text-center my-3">
            <div className="row">
                <div className="col">
                    <span style={{ color: '#fff' }}>Genre   </span>
                    <select value={genre} onChange={handleChange} onClick={listChange}>
                        <option value="Any" >Any</option>
                        <option value="Action" >Action</option>
                        <option value="Adventure" >Adventure</option>
                        <option value="Comedy" >Comedy</option>
                        <option value="Drama" >Drama</option>
                        <option value="Fantasy" >Fantasy</option>
                        <option value="Horror" >Horror</option>
                        <option value="Mahou Shoujo" >Mahou Shoujo</option>
                        <option value="Mecha" >Mecha</option>
                        <option value="Music" >Music</option>
                        <option value="Mystery" >Mystery</option>
                        <option value="Psychological" >Psychological</option>
                        <option value="Romance" >Romance</option>
                        <option value="Sci-Fi" >Sci-Fi</option>
                        <option value="Slice of Life" >Slice of Life</option>
                        <option value="Sports" >Sports</option>
                        <option value="Supernatural" >Supernatural</option>
                        <option value="Thriller" >Thriller</option>
                    </select>
                </div>
                <div className="col">
                    <label>
                        <span style={{ color: '#fff' }}>Type   </span>
                        <select onChange={handleChange2}>
                            <option value="TV">TV Series</option>
                            <option value="MOVIE">Movie</option>
                            <option value="OVA">OVA</option>
                            <option value="ONA">ONA</option>
                            <option value="SPECIAL">Specials</option>
                        </select>
                    </label>

                </div>
                <div className="col">
                    <span style={{ color: '#fff' }}>Status   </span>
                    <select >
                        <option value="any">Any</option>
                        <option value="finished">Finished Airing</option>
                        <option value="airing">Currently Airing</option>
                        <option value="notyetaired">Not yet aired</option>


                    </select>
                </div>
                <div className="col">
                    <span style={{ color: '#fff' }}>Year  </span>
                    <select >
                        <option value="1990">1990</option>
                        <option value="1991">1991</option>
                        <option value="1992">1992</option>
                        <option value="1993">1993</option>
                        <option value="1994">1994</option>
                        <option value="1995">1995</option>
                        <option value="1996">1996</option>
                        <option value="1997">1997</option>
                        <option value="1998">1998</option>
                        <option value="1999">1999</option>
                        <option value="2000">2000</option>
                        <option value="2001">2001</option>
                        <option value="2002">2002</option>
                    </select>
                </div>
            </div>
            <div className="row my-2 visually-hidden">
                <div classname="col ">
                    {match.map((item) => {
                        return (
                            <div className='mx-1' style={{
                                float: 'left',
                                padding: '1px 5px 1px 5px',
                                backgroundColor: '#2596be',
                                borderRadius: '5px'
                            }}>
                                <h6 className='text-light'>{item + " "}</h6>
                            </div>
                        )

                    })}
                </div>
            </div>
            <div className="row">
                <div className="col-sm-12">
                    <div className="container">
                        <span className="text-light visually-hidden"><h2>Genre selected: {newgenre}</h2>
                        </span>
                        {/* first row left */}
                        <div className="row ">
                            {/* first card */}
                            {result?.length === 0 ? <div class="d-flex justify-content-center my-5">
                                <div class="spinner-border text-primary" style={{marginTop:'45vh'}} role="status">
                                    <span class="visually-hidden">Loading...</span>
                                </div>
                            </div> : result?.data?.Page?.media?.map((item) => {
                                return (
                                    <div className="col-sm-2 my-2">

                                        <div className="card border border-black border-2" style={{ width: '11rem', height: '18rem' }}>
                                            <Link to={`/info/${item.id}`}>
                                                <img src={item?.coverImage?.large} title={item?.title?.userPreferred} className="card-img-top" alt={item?.title?.userPreferred} style={{ maxHeight: '178px' }} />
                                            </Link>
                                            <div className="card-body text-light bg-black">
                                                <h6 className="card-title">{item?.title?.userPreferred.length > 30 ? item?.title?.userPreferred.slice(0, 25) + '...' : item?.title?.userPreferred}</h6>
                                                <p className="card-text"></p>
                                                {/*<a href="/info/{{i.animeId}}" class="btn btn-primary">Watch</a>-->*/}
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}







                        </div>






                    </div>

                </div>
                {/* first row right */}

            </div>
        </div>
    )
}

export default Filter