import React from 'react';
import axios from 'axios';
import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Link, useLocation } from 'react-router-dom'
import logo from '../Images/logo_main.png'
import favContext from '../Context/Favourites/favcontext';
import { useContext } from 'react';


const Navbar = () => {
  const context = useContext(favContext)
  const {}  = context

  let uname = localStorage.getItem('username')
  console.log(uname)

  let location = useLocation()
  useEffect(() => {
    console.log(location.pathname)
  
    
  }, [location])
  

  const [searchTerm, setSearchTerm] = useState('');
  const [showresult, setShowresult] = useState('hidden')

  const navigate = useNavigate();

  const handleSubmit = (e) => {
    e.preventDefault();
    if (searchTerm) {
      navigate(`/search/${searchTerm}`);
      setSearchTerm('');
    }

  }

  const handlelogout = ()=>{
    localStorage.removeItem('token');
    navigate("/")
  }



  const [searchresult, setSearchresult] = useState([])

  useEffect(() => {
    const result = {
      method: 'POST',
      url: 'https://anilist-graphql.p.rapidapi.com/',
      headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
        'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
      },
      data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $search: String\
        ) \
        {\
          Page(page: $page, perPage: $perPage) {\
            pageInfo {\
              total\
              currentPage\
              lastPage\
              hasNextPage\
              perPage\
            }\
            media(id: $id, type: $type, search: $search) {\
              id\
              title {\
                userPreferred\
              }\
              \
            }\
           \
            \
          }\
        }\",\"variables\":{\"search\":\"${searchTerm}",\"page\":1,\"perPage\":3,\"type\":\"ANIME\"}}`
    };

    axios.request(result).then(function (response) {
      setSearchresult(response.data);
    }).catch(function (error) {
      console.error(error);
    });
  }, [searchTerm])

  console.log(searchresult)
  console.log(showresult)

  return (
    <div>
      <nav className="navbar navbar-expand-lg bg-black sticky-top">
        <div className="container-fluid">
          <Link to="/">
            <img src={logo} alt='' style={{ width: '211px', height: '63px' }} />
          </Link>

          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon "></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className={`nav-link text-${location.pathname === "/" ? "primary" : "light"}`} to="/">Home</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link text-${location.pathname === "/movies" ? "primary" : "light"}`} to="/movies">Movies</Link>
              </li>
              <li className="nav-item">
                <Link className={`nav-link text-${location.pathname === "/filter" ? "primary" : "light"}`} to="/filter">Filter</Link>

              </li>
              
                {!localStorage.getItem('token') ? <li className="nav-item"><Link className={`nav-link text-${location.pathname === "/login" ? "primary" : "light"}`} to="/login">Log In</Link></li>  :
                <>
                <li className="nav-item">
                  <Link className='nav-link text-light' onClick={handlelogout} to="/">Log Out</Link>
                </li>
                <li className="nav-item">
                  <Link className={`nav-link text-${location.pathname === "/favourites" ? "primary" : "light"}`} to="/favourites">Favourites</Link>
                </li>
                </>
                }

              
                
              
              
              
            </ul>
            {!localStorage.getItem('token') ? <span></span> : <ul className='mx-2'>
            <li className='nav-item'>
                <span className='text-light my-3'>Logged in as {localStorage.getItem('username')}</span>
            </li>
            </ul>}
            
            <form onSubmit={handleSubmit} method="get" className="d-flex" role="search" >
              <input className="form-control me-2" name="value" id="value" type="search" data-bs-toggle="collapse" placeholder="Search Anime" aria-label="Search" onChange={(e) => setSearchTerm(e.target.value)} onClick={() => { setShowresult('visible') }} />
              <button className="btn btn-outline-primary" type="submit" ><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-search" viewBox="0 0 16 16">
                <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z" />
              </svg></button>

            </form>

          </div>

        </div>

      </nav>
      <div style={{ position: 'absolute', float: 'left', width: '400px', zIndex: 1, visibility: `${showresult}`, marginLeft: '67rem' }} >

        {searchresult?.data?.Page?.media.map((item, idx) => {
          return (
            <Link to={`/info/${item?.id}`} onClick={function () { setShowresult('hidden'); setSearchresult('') }}>
              <a class="list-group-item list-group-item-action bg-light">{item?.title?.userPreferred}</a>
            </Link>
            /**<a className='list-group-item list-group-item-action bg-light' href={`/info/${item?.id}`}>{item?.title?.userPreferred}</a>**/

          )
        })}
        <form>
          {/**<a href={`/search/${searchTerm}`} class="list-group-item list-group-item-action bg-light">See all results</a>**/}
          <Link to={`/search/${searchTerm}`} className='list-group-item list-group-item-action bg-light'>See all results</Link>
        </form>


      </div>
    </div>




  )
}

export default Navbar