import React from 'react';
import axios from 'axios';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';






const Format = (props) => {

  const [format, setFormat] = useState([]);
  const [page, setPage] = useState(1)
  useEffect(() => {
    const options = {
      method: 'POST',
      url: 'https://anilist-graphql.p.rapidapi.com/',
      headers: {
        'content-type': 'application/json',
        'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
        'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
      },
      data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $format: MediaFormat, $sort: [MediaSort]) \
    {\
    Page(page: $page, perPage: $perPage) {\
    pageInfo {\
      total\
      currentPage\
      lastPage\
      hasNextPage\
      perPage\
    }\
    media(id: $id, type: $type, format: $format, sort: $sort ) {\
      id\
      title {\
        \
        userPreferred\
      } \
      format\
      duration\
      coverImage {\
        large\
      } \
      \
      averageScore\
    }\
    \
    \
    }\
    }\",\"variables\":{\"page\":${page.toString()},\"perPage\":24,\"type\":\"ANIME\",\"format\":\"${props.format}\",\"sort\":\"${props.sort}\"}}`
    };

    axios.request(options).then(function (response) {
      setFormat(response.data);
    }).catch(function (error) {
      console.error(error);
    });
  }, [page])

  if (format?.length == 0) {
    <h1 style={{ color: 'white' }}>Loading</h1>
  }

  console.log(format)
  console.log(props.label)

  return (

    <div className="container">
      <div className="row">
        <div className="col-sm-12">
          <div className="container">
            <span className="text-light"><h2>{props.label}</h2>
            </span>
            <p id='demo' style={{ color: '#fff' }}></p>
            <div className="row ">
              {/* first card */}
              {format?.length == 0 ? <div class="d-flex justify-content-center my-5">
                <div class="spinner-border text-primary" style={{marginTop:'38vh'}} role="status">
                  <span class="visually-hidden">Loading...</span>
                </div>
              </div> : format?.data?.Page?.media.map((item) => {
                return (
                  <div className="col-sm-2 my-2">

                    <div className="card  border-white border-2 " style={{ width: '11rem', height: '18rem' }}>
                    <Link to={`/info/${item.id}`}>
                    <img src={item?.coverImage?.large} title={item?.title?.userPreferred} className="card-img-top" alt={item?.title?.userPreferred} style={{ maxHeight: '178px' }} />
                    </Link>
                      <div className="card-body text-light bg-black">
                        <h6 className="card-title">{item?.title?.userPreferred.length > 30 ? item?.title?.userPreferred.slice(0, 25) + '...' : item?.title?.userPreferred}</h6>
                        <p className="card-text"></p>
                        {/*<a href="/info/{{i.animeId}}" class="btn btn-primary">Watch</a>-->*/}
                      </div>
                    </div>
                  </div>
                )


              })}




              {/* first card end */}





            </div>






          </div>

        </div>
        {/*first row right */}

      </div>
      <nav aria-label="Page navigation example">
        <ul class="pagination justify-content-center">
          <li class="page-item"><a class="page-link" href='#' onClick={function () { format?.data?.Page?.pageInfo?.currentPage == 1 ? setPage(1) : setPage(page - 1); setFormat([]) }}>Previous</a></li>
          <li class="page-item active"><a class="page-link" >{page}</a></li>
          <li class="page-item"><a class="page-link" href='#' onClick={function () { format?.data?.Page?.pageInfo?.currentPage == format?.data?.Page?.pageInfo?.lastPage ? setPage(format?.data?.Page?.pageInfo?.lastPage) : setPage(page + 1); setFormat([]) }} >Next</a></li>
        </ul>
      </nav>
             
    </div>
    
    
  )
}

export default Format