import React from 'react'
import { useState, useEffect } from 'react'
import { useParams, Link } from 'react-router-dom'
import { fetchFromAPI } from '../Utilities/fetchFromAPI'
import axios from 'axios'

const Searchfeed = () => {

    const [searchresult, setSearchresult] = useState([]);
    const { searchTerm } = useParams();

    useEffect(() => {
        const options = {
            method: 'POST',
            url: 'https://anilist-graphql.p.rapidapi.com/',
            headers: {
                'content-type': 'application/json',
                'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
                'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
            },
            data: `{\"query\":\"query ($id: Int, $page: Int, $perPage: Int, $type: MediaType, $search: String) \
            {\
            Page(page: $page, perPage: $perPage) {\
            pageInfo {\
              total\
              currentPage\
              lastPage\
              hasNextPage\
              perPage\
            }\
            media(id: $id, type: $type, search: $search ) {\
              id\
              title {\
                \
                userPreferred\
              }\
              \
              \
              coverImage {\
                large\
              } \
              \
              averageScore\
            }\
            \
            \
            }\
            }\",\"variables\":{\"search\":\"${searchTerm}\",\"page\":1,\"perPage\":20,\"type\":\"ANIME\"}}`
        };

        axios.request(options).then(function (response) {
            setSearchresult(response.data);
        }).catch(function (error) {
            console.error(error);
        });
    }, [searchTerm]);

    console.log(searchresult)
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12">
                    <div className="container">
                        <span className="text-light"><h2>Search Results for {searchTerm}:</h2>
                        </span>
                        {/* first row left */}
                        <div className="row" style={{marginBottom:'10rem'}}>
                            {/* first card */}
                            {searchresult?.length === 0 ? <div class="d-flex justify-content-center my-5">
                                <div class="spinner-border text-primary" style={{marginTop:'39.5vh', marginBottom:'2vh'}} role="status">
                                    <span class="visually-hidden">Loading...</span>
                                </div>
                            </div> : searchresult?.data?.Page?.media?.map((item) => {
                                return (
                                    <div className="col-sm-2 my-2">

                                        <div className="card  border-black border-2" style={{ width: '11rem', height: '18rem' }}>
                                            <Link to={`/info/${item.id}`}>
                                                <img src={item?.coverImage?.large} title={item?.title?.userPreferred} className="card-img-top" alt={item?.title?.userPreferred} style={{ maxHeight: '178px' }} />
                                            </Link>
                                            <div className="card-body text-light bg-dark">
                                                <h6 className="card-title">{item?.title?.userPreferred.length > 30 ? item?.title?.userPreferred.slice(0, 25) + '...' : item?.title?.userPreferred}</h6>
                                                <p className="card-text"></p>
                                                {/*<a href="/info/{{i.animeId}}" class="btn btn-primary">Watch</a>-->*/}
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}







                        </div>






                    </div>

                </div>
                {/* first row right */}

            </div>

        </div>
    )
}

export default Searchfeed