import React from 'react'
import { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams, Link } from 'react-router-dom';
import { useContext } from 'react';
import favContext from '../Context/Favourites/favcontext';
import { Alert } from 'bootstrap';



const Animedetail = (props) => {
    const context = useContext(favContext);
    
    
    const addfav = async (animedet) => {
        const response = await fetch('http://localhost:3001/api/favourites/addfav',
            {
                method: 'POST',
                headers: {
                    "Content-Type": "application/json",
                    "auth-token": localStorage.getItem('token')
                },
                body: JSON.stringify(animedet)
            });
        const json = await response.json()
        console.log(json)
        if(json.success){
            //save auth token
            props.showalert("Added successfully", "success")
        }
        else{
            props.showalert("Log In first!", "danger")
        }
        console.log("added!")

    }

    

    const [detail, setDetail] = useState([]);
    const { animeid } = useParams();


    useEffect(() => {
        const options = {
            method: 'POST',
            url: 'https://anilist-graphql.p.rapidapi.com/',
            headers: {
                'content-type': 'application/json',
                'X-RapidAPI-Key': 'f77c5bde9bmsh775458a2f3f1651p175e25jsn8b6a2f52c501',
                'X-RapidAPI-Host': 'anilist-graphql.p.rapidapi.com'
            },
            data: `{\"query\":\"query ($id: Int, $type: MediaType) {\
                Media(id: $id, type: $type) {\
                id\
                title {\
                  userPreferred\
                    romaji\
                }\
                episodes\
                format\
                  duration\
                status\
                description\
                season\
                seasonYear\
                genres\
                trailer {\
                  id\
                }\
                bannerImage\
                studios {\
                  edges {\
                    node {\
                      name\
                    }\
                  }\
                }\
                coverImage {\
                  extraLarge\
                }\
                averageScore\
                streamingEpisodes {\
                  title\
                  thumbnail\
                  url\
                }\
                externalLinks {\
                  site\
                  url\
                  icon\
                  color\
                }\
                recommendations {\
                  edges {\
                    node {\
                      mediaRecommendation{\
                        id\
                        title {\
                          userPreferred\
                        }\
                        coverImage {\
                          large\
                        }\
                      }\
                    }\
                  }\
                }\
                }\
                }\",\"variables\":{\"id\":"${animeid}",\"type\":\"ANIME\"}}`
        };

        axios.request(options).then(function (response) {
            setDetail(response.data);
        }).catch(function (error) {
            console.error(error);
        });



    }, [animeid])

    const animedet = {
        "title": `${detail?.data?.Media?.title?.userPreferred}`,
        "titleId": `${detail?.data?.Media?.id}`,
        "genre": "Romance, Comedy",
        "rating": 8,
        "imageId": `${detail?.data?.Media?.coverImage?.extraLarge}`
    }

    const submitfav = ()=>{
        addfav(animedet)
    }

    console.log(detail)

    return (
        
        <div className="container text-light bg-dark" >
            
            {/* Banner Image */}
            <div className='row'>
                <img src={detail?.data?.Media?.bannerImage} alt={''} style={{ height: '18rem', maxWidth: '80rem', marginLeft: 'auto', marginRight: 'auto', display: 'block' }} />
            </div>



            <div className="row my-3" >
                {/*Left card where image and data is put */}
                <div className="container col-sm-4 mx-0" style={{ position: 'relative', top: '-10rem' }}>
                    <div className="card text-light bg-dark" style={{ width: '18rem' }}>
                        <img src={detail?.data?.Media?.coverImage?.extraLarge} className="card-img-top" alt={''} style={{ border: '3px solid white' }} />
                        <div className="card-body">

                            <p className="fst-normal fs-2">{detail?.data?.Media?.title?.userPreferred}</p>
                            <p>Type: {detail?.data?.Media?.format}</p>
                            <p>Status: {detail?.data?.Media?.status}</p>
                            <p>Released Date: {detail?.data?.Media?.seasonYear}</p>
                            <p>Other names: {detail?.data?.Media?.title?.romaji}</p>
                            <p>Episodes: {detail?.data?.Media?.episodes}</p>
                            <p>Genres: {detail?.data?.Media?.genres.map((item) => { return (<span><a href='#'>{item}</a>, </span>) })}</p>
                            <p>Duration: {detail?.data?.Media?.duration} mins</p>
                            <p>Studio: {detail?.data?.Media?.studios?.edges.map((item, idx) => {
                                return (<span>{item?.node?.name}, </span>)
                            })}</p>
                            <p>Rating: {detail?.data?.Media?.averageScore}/100</p>
                            <p>External Links:<br />
                                {detail?.data?.Media?.externalLinks.map((item) => {
                                    return (
                                        <a href={item?.url} className="btn btn-outline-light my-1 mx-1" title={item?.site} style={{ backgroundColor: `${item?.color}` }}>{item?.site}</a>
                                    )
                                })}</p>

                        </div>
                    </div>
                </div>

                {/*Right Side where sypnosis, episodes and reccomendations and trailer is put*/}
                <div className="container col-sm-8 text-light bg-dark">

                    <div>
                        
                        <a onClick={submitfav} className="btn btn-dark btn-outline-light" title="Add to Favourites">Add to favorites</a>
                        <p className="fs-3 my-2">
                            Synopsis:
                        </p>
                        <p>
                            {detail?.data?.Media?.description}
                        </p>
                    </div>

                    <div className="container ">
                        <div className="row bg-dark">
                            <h3>Episodes</h3>
                            {/* for i in kkk */}
                            {detail?.length == 0 ? <div class="d-flex justify-content-center my-5">
                                <div class="spinner-border text-primary" role="status">
                                    <span class="visually-hidden">Loading...</span>
                                </div>
                            </div> : detail?.data?.Media?.streamingEpisodes.map((item, idx) => {
                                return (
                                    <div className="col-sm-3 my-1">
                                        <div className="card bg-dark" style={{ width: '12rem', borderColor: 'aliceblue', height: '15rem' }} key={idx}>
                                            <a href={item?.url}><img src={item?.thumbnail} className="card-img-top"
                                                alt={item?.title} /></a>
                                            <div className="card-body text-light bg-black">
                                                <p className="card-text">{item?.title.length > 40 ? item?.title.slice(0, 25) + '...' : item?.title}</p>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}

                            {/* endfor */}
                        </div>
                        <br />
                    </div>

                    <div className="container">
                        <h3>Trailer</h3>
                        <iframe width="500" height="315"
                            src={`https://www.youtube.com/embed/${detail?.data?.Media?.trailer?.id}`}>
                        </iframe>
                    </div>

                </div>

                {/*Recommendatiosn which take the whole row*/}


            </div>

            <div className="row bg-dark">
                <h3>Recommendations</h3>
                {/* for i in nnn */}
                {detail?.length == 0 ? <div class="d-flex justify-content-center my-5">
                    <div class="spinner-border text-primary" role="status">
                        <span class="visually-hidden">Loading...</span>
                    </div>
                </div> : detail?.data?.Media?.recommendations?.edges.map((item) => {
                    return (
                        <div className="col-sm-2 my-2">
                            <div className="card  border-black border-2 " style={{ width: '11rem', height: '18rem' }}>
                                <Link to={`/info/${item?.node?.mediaRecommendation?.id}`} onClick={() => (window.location.href = '#top')}><img onClick={() => (setDetail([]))} src={item?.node?.mediaRecommendation?.coverImage?.large} title={item?.node?.mediaRecommendation?.title?.userPreferred} className="card-img-top" alt={item?.node?.mediaRecommendation?.title?.userPreferred} style={{ maxHeight: '178px' }} /></Link>
                                <div className="card-body text-light bg-black">
                                    <h6 className="card-title">{item?.node?.mediaRecommendation?.title?.userPreferred.length > 40 ? item?.node?.mediaRecommendation?.title?.userPreferred.slice(0, 25) + '...' : item?.node?.mediaRecommendation?.title?.userPreferred}</h6>
                                    <p className="card-text"></p>
                                    {/*<a href="/info/{{i.animeId}}" className="btn btn-primary">Watch</a>*/}
                                </div>
                            </div>
                        </div>
                    )
                })}

                {/* endfor */}
            </div>

        </div>
    )
}

export default Animedetail